* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

h1,
h2 {
  margin: 0;
  padding: 0;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list__item {
  display: flex;
  align-items: center;
  text-align: center;
  margin-bottom: 10px;
}

.country-list__item h2 {
  margin-left: 10px;
}

.country-card {
  display: flex;
  align-items: center;
  text-align: center;
}

.country-card__name {
  margin-left: 10px;
}

.country-property {
  font-weight: bold;
}
